<template>
  <div class="hello">
    <h3>ลงทะเบียน</h3>
    <h1>{{ userId }}</h1>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    return {
      title: "ลงทะเบียน",
      userId: "",
      profile: [],
      send: false,
      tel: ""
    };
  },
  created() {
    let vm = this;
    window.onload = function(e) {
      liff.init(function(data) {
        vm.initializeApp(data);
      });
    };
  },
  methods: {
    initializeApp: function(data) {
      let vm = this;
      //vm.name.push(data);
      vm.userId = data.context.userId;
      console.log(data);
    }
  }
};
</script>




